@import url("https://fonts.googleapis.com/css2?family=DM+Sans&display=swap");
html {
  display: flex;
  justify-content: center;
}

body {
  font-family: DM Sans, sans-serif;
  max-width: 1440px;
  width: 100%;
}
body button {
  background-color: transparent;
  border: 0;
  color: #901c1c;
  cursor: pointer;
  padding: 0;
}

.btContactMobile {
  width: 170px;
  height: 35px;
  border-radius: 5px;
  background-color: #901c1c;
  color: white;
  font-weight: bold;
  font-size: 18px;
  position: fixed;
  bottom: 30px;
  left: 50vw;
  transform: translateX(-50%);
}

.photographerLink {
  width: 12.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.photographerLink .imgContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12.5rem;
  height: 12.5rem;
  margin-bottom: 1.25rem;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.2);
}
.photographerLink .imgContainer img {
  position: relative;
  max-width: none;
}
.photographerLink .imgContainer .mimi {
  width: 19rem;
  top: 1.0625rem;
}
.photographerLink .imgContainer .ellie {
  width: 13.5625rem;
  left: 0.1875rem;
  top: 0.25rem;
}
.photographerLink .imgContainer .tracy {
  width: 32.375rem;
  top: 1.6875rem;
}
.photographerLink .imgContainer .nabeel {
  width: 32.875rem;
  top: 1.8125rem;
}
.photographerLink .imgContainer .rhode {
  width: 33.1875rem;
  top: 13.1875rem;
  left: 2.25rem;
}
.photographerLink .imgContainer .marcel {
  width: 33.75rem;
  top: 1.75rem;
}
.photographerLink h2 {
  color: #d3573c;
  font-size: 2.25rem;
  white-space: nowrap;
}

.filterLink {
  border: 0.5px solid #c4c4c4;
  border-radius: 0.6875rem;
  margin: 0 0.125rem;
  padding: 0.125rem 0.375rem;
}
.filterLink:focus, .filterLink:hover {
  background-color: #901c1c;
  color: #fff;
}
@media screen and (min-width: 992px) {
  .filterLink {
    margin: 0.125rem;
  }
}

.logo {
  align-items: center;
  display: flex;
  grid-area: logo;
  height: 109px;
}
@media screen and (min-width: 992px) {
  .logo {
    height: 137px;
  }
  .logo img {
    fill: none;
    height: 50px;
    width: 200px;
  }
}

.photographerArticle {
  flex: 1 0 25%;
  display: flex;
  flex-direction: column;
  width: 25%;
  min-width: 100%;
  align-items: unset;
  height: 9.4rem;
  margin: 0;
  position: relative;
  bottom: 6px;
}
@media screen and (min-width: 992px) {
  .photographerArticle {
    background-color: #fafafa;
    height: auto;
    border-radius: 0.3125rem;
  }
}
.photographerArticle .imgNameContainer {
  display: flex;
  position: relative;
  bottom: 0.625rem;
  flex-direction: row-reverse;
  align-items: flex-start;
  width: 100%;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}
.photographerArticle .imgNameContainer .imgContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.25rem;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.2);
  width: 6.25rem;
  height: 6.25rem;
}
.photographerArticle .imgNameContainer .imgContainer img {
  position: relative;
  max-width: none;
}
.photographerArticle .imgNameContainer .imgContainer .mimi {
  top: 0.5rem;
  width: 10.625rem;
}
.photographerArticle .imgNameContainer h2 {
  color: #d3573c;
  font-size: 2.6rem;
  position: relative;
  top: 0.625rem;
  white-space: nowrap;
  height: 47px;
}
@media screen and (min-width: 992px) {
  .photographerArticle .imgNameContainer h2 {
    font-size: 64px;
    margin-top: 56px;
    margin-left: 3.47%;
    height: 83px;
  }
}
.photographerArticle p {
  margin: 1.5px 0;
}
.photographerArticle .location,
.photographerArticle .tagline,
.photographerArticle .photographerNav {
  position: relative;
  bottom: 4.6875rem;
  margin-bottom: 0.25rem;
  font-size: 0.8125rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .location,
.photographerArticle .tagline,
.photographerArticle .photographerNav {
    margin-left: 3.47%;
    position: unset;
  }
}
.photographerArticle .location {
  color: #901c1c;
  line-height: 0.9375rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .location {
    font-size: 1.5rem;
    margin-bottom: 1.1875rem;
    height: 1.9375rem;
  }
}
.photographerArticle .tagline {
  color: #757575;
  margin-bottom: 0.4375rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .tagline {
    font-size: 1.125rem;
    margin-bottom: 0.9375rem;
  }
}
.photographerArticle .price {
  display: none;
}
@media screen and (min-width: 992px) {
  .photographerArticle .photographerNav {
    margin-bottom: 3.25rem;
  }
}
@media screen and (min-width: 992px) {
  .photographerArticle .photographerNav .filterLink {
    font-size: 1.125rem;
    font-weight: bold;
  }
}

.photographerSection {
  flex: 1 0 25%;
  display: flex;
  flex-direction: column;
  min-width: 20.9375rem;
  width: 20.9375rem;
  max-width: 20.9375rem;
  margin: 5px;
}
.photographerSection .imgContainer {
  height: 11.9375rem;
  width: 100%;
  overflow: hidden;
  border-radius: 5px;
}
.photographerSection .imgContainer img {
  position: relative;
}
.photographerSection .imgContainer .animalRainbow {
  bottom: 2.9375rem;
}
.photographerSection .infoContainer {
  display: flex;
  justify-content: space-between;
  margin-top: 0.625rem;
  align-items: center;
}
.photographerSection .infoContainer p {
  color: #901c1c;
}
.photographerSection .infoContainer .imgTitle,
.photographerSection .infoContainer .like {
  font-size: 0.875rem;
}
.photographerSection .infoContainer .price {
  font-size: 0.75rem;
  position: relative;
  left: 5.5625rem;
}
.photographerSection .infoContainer .like::after {
  content: "\f004";
  font-family: "Font Awesome 5 Free";
  font-weight: bold;
  margin-left: 0.3125rem;
  font-size: 0.6875rem;
}

header {
  margin: 0 20px;
  padding-top: 3px;
}
@media screen and (min-width: 992px) {
  header {
    margin: 0 100px;
  }
}

.photographerMain {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}